.cardChef {
  cursor: pointer;
  position: relative;
  display: flex;
  flex-direction: column-reverse;
  min-width: 478px;
  height: 372px;
}
.chefImg {
  z-index: -1;
  position: absolute;
  width: 100%;
  height: 100%;
}
.buttomCard {
  width: 100%;
  height: 78px;
  text-align: center;
  background: rgba(255, 255, 255, 0.8);
  transition: 0.5s;
}
.buttomCard > h3 {
  margin-top: 15px;
}
.cardChef:hover .buttomCard {
  height: 120px;
}
